<script>
  export let title;
</script>

<style>
  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(230, 230, 230, 0.75);
    z-index: 10;
  }
  .modal {
    position: fixed;
    top: 10vh;
    left: 10%;
    width: 80%;
    max-height: 80vh;
    background: #fff;
    z-index: 20;
    overflow-y: scroll;
  }

  .content,
  footer,
  header {
    padding: 1rem;
  }
  @media (min-width: 768px) {
    .modal {
      width: 40rem;
      left: calc(50% - 20rem);
    }
  }
</style>

<div class="backdrop" />

<div class="modal">
  <header>
    <slot name="header">
      <h1>{title}</h1>
    </slot>
  </header>
  <div class="content">
    <slot />
  </div>
  <footer>
    <slot name="footer">
      <button>Close</button>
    </slot>
  </footer>

</div>
