<script>
  import { createEventDispatcher, onDestroy } from "svelte";
  import Calendar from "./Calendar.svelte";
  import Button from "../UI/Button.svelte";

  export let image;
  export let id;
  export let name;
  export let surname;

  let dispatch = createEventDispatcher();
  function showChildDetails() {
    dispatch("showChildDetails", {
      id
    });
  }
</script>

<style>
  @import url("https://fonts.googleapis.com/css?family=Lato&display=swap");
  .child {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
    margin-top: 1rem;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  .child > .image,
  .child > .id,
  .child > .name,
  .child > .surname {
    display: inline-block;
  }

  .child > .image > img {
    width: 5rem;
    height: 6rem;
  }
  p {
    font-family: "Lato", sans-serif;
  }
</style>

<div class="child">
  <div class="image">
    <img src={image} alt="image" />
  </div>
  <div class="id">
    <p>Id: {id}</p>
  </div>
  <div class="name">
    <p>Name: {name}</p>
  </div>
  <div class="surname">
    <p>Surname: {surname}</p>
  </div>
  <Button type="button" caption="Details" on:click={showChildDetails} />
</div>

<Calendar />
