<script>
  import { createEventDispatcher } from "svelte";
  import Button from "../UI/Button.svelte";
  export let name = "";
  export let surname;
  export let age = "";
  export let id = "";
  export let image = "";

  let dispatch = createEventDispatcher();

  function closeDetails() {
    dispatch("closeDetails");
  }

  function editDetails() {
    dispatch("editDetails", {
      id
    });
  }
</script>

<style>
  .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 1rem;
  }
  .image > img {
    display: block;
    width: 100%;
    height: auto;
  }
</style>

<div class="grid">
  <!--  on left iamge -->
  <div class="left">
    <div class="image">
      <img src={image} alt="image" />
    </div>
  </div>
  <!-- on right  -->
  <div class="right">

    <div class="personal">
      <!-- Id -->
      <div class="id">
        <h3>{id}</h3>
      </div>
      <!-- name -->
      <div class="name">
        <h3>Name: {name}</h3>
      </div>
      <!-- surname -->
      <div class="Surname">
        <h3>Surname: {surname}</h3>
      </div>
      <div class="age">
        <h3>Age: {age}</h3>
      </div>
    </div>
    <div class="actions">
      <Button caption="Close" on:click={closeDetails} />
      <Button caption="Edit" address="edit" on:click={editDetails} />
    </div>
  </div>
</div>
